import uniRate from '../../components/uni-rate/uni-rate.vue'
 
export default {
	name: 'index',
	components: {
		uniRate
	},
	data() {
		return {
			banners: [],
			movieList: [], //电影
			trailer: [], // 预告
			likes: [], // 猜你喜欢
			isPull: false,
			
		};
	},
	onLoad() {
		this.getData();
		this.getMovieList();
		this.getTrailer();
		this.getLikes();
	},
	onPullDownRefresh() {
		this.isPull = true
		this.getLikes()
	},
	onHide () {
		if (this.video) {
			this.video.pause()
		}
	},
	methods: {
		// 视频播放
		play (id) {
			this.video = uni.createVideoContext(id)
			this.trailer.map(item => {
				if (item.id !== id) {
					uni.createVideoContext(item.id).pause()
				}
			})
		},
		// 去详情页
		goDetail(item) {
			uni.navigateTo({
				url: '/pages/detail/detail?item=' + JSON.stringify(item)
			});
		},
		// 获取数据
		getData() {
			uni.showLoading({
				title:"加载中..."
			})
			uni.request({
				url: `${this.$api}/index/carousel/list`,
				method: 'POST',
				data: {
					qq: '7929290'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					if (res.data.status === 200) {
						uni.hideLoading()
						this.banners = res.data.data;
					}
				}
			});
		},
		// 获取电影列表
		getMovieList() {
			uni.showLoading({
				title:"加载中..."
			})
			uni.request({
				url: `${this.$api}/index/movie/hot`,
				method: 'POST',
				data: {
					qq: '7929290',
					type: 'superhero'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					if (res) {
						uni.hideLoading()
						this.movieList = res.data.data
					}
					
				}
			});
		},
		 // 获取播放视频
		getTrailer() {
			uni.showLoading({
				title:"加载中..."
			})
			uni.request({
				url: `${this.$api}/index/movie/hot`,
				method: 'POST',
				data: {
					qq: '7929290',
					type: 'trailer'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					if (res) {
						uni.hideLoading()
						this.trailer = res.data.data;
					}
					
				}
			});
		},
		// 获取猜你喜欢
		getLikes() {
			uni.request({
				url: `${this.$api}/index/guessULike`,
				method: 'POST',
				data: {
					qq: '7929290',
					type: 'trailer'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					if (res) {
						uni.stopPullDownRefresh()
						this.likes = res.data.data;
						this.likes.map(item => {
							item.createTime = this.$moment(item.createTime).format('YYYY-MM-DD');
						})
						if (this.isPull) {
							setTimeout(() => {
								uni.showToast({
									icon: 'success',
									title: '刷新成功'
								})
							}, 500)
						}
					}
				}
			})
		},
		// 点赞
		praiseMe () {
			uni.showToast({
				icon:'success',
				title: '点赞成功'
			})
		}
	}
};
